* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {

}

@font-face {
  font-family: raleway_f;
  src: url('./components/fonts/raleway-regular.woff2');
}
@font-face {
  font-family: raleway_f;
  src: url('./components/fonts/raleway-black.woff2');
  font-weight: 900;
}

.App {
  background-color: #010101;
  color: white;
  font-family: raleway_f, sans-serif;
  line-height: 1.55;
}

.main-header {
  position: relative;
}

.layers {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.layer {
  height: 100%;
  width: 100%;
  position: absolute;
  background-size: cover;
  background-position: center;
  will-change: transform;
  z-index: 2;
}

.layers_base {
  transform: translate3d(0, calc(var(--scrollTop)/1.6), 0);
  z-index: 0;
}

.layers_middle {
  transform: translate3d(0, calc(var(--scrollTop)/2.5), 0);
}

.layers_front {
  transform: translate3d(0, calc(var(--scrollTop)/5.7), 0);
}

.layers_header {
  z-index: 1;
  transform: translate3d(0, calc(var(--scrollTop)/2), 0);
}

.main-article {
  min-height: 100vh;
}

.layer.layers_base {
  background-image: url("./components/img/layer-base.png");
}
.layer.layers_middle {
  background-image: url("./components/img/layer-middle.png");
}
.layer.layers_front {
  background-image: url("./components/img/layer-front.png");
}

.main-article {
  background-image: url("./components/img/dungeon.jpg");
}
