body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {

}

@font-face {
  font-family: raleway_f;
  src: url(/parallax_web/static/media/raleway-regular.957a49ea.woff2);
}
@font-face {
  font-family: raleway_f;
  src: url(/parallax_web/static/media/raleway-black.294f721c.woff2);
  font-weight: 900;
}

.App {
  background-color: #010101;
  color: white;
  font-family: raleway_f, sans-serif;
  line-height: 1.55;
}

.main-header {
  position: relative;
}

.layers {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.layer {
  height: 100%;
  width: 100%;
  position: absolute;
  background-size: cover;
  background-position: center;
  will-change: transform;
  z-index: 2;
}

.layers_base {
  -webkit-transform: translate3d(0, calc(var(--scrollTop)/1.6), 0);
          transform: translate3d(0, calc(var(--scrollTop)/1.6), 0);
  z-index: 0;
}

.layers_middle {
  -webkit-transform: translate3d(0, calc(var(--scrollTop)/2.5), 0);
          transform: translate3d(0, calc(var(--scrollTop)/2.5), 0);
}

.layers_front {
  -webkit-transform: translate3d(0, calc(var(--scrollTop)/5.7), 0);
          transform: translate3d(0, calc(var(--scrollTop)/5.7), 0);
}

.layers_header {
  z-index: 1;
  -webkit-transform: translate3d(0, calc(var(--scrollTop)/2), 0);
          transform: translate3d(0, calc(var(--scrollTop)/2), 0);
}

.main-article {
  min-height: 100vh;
}

.layer.layers_base {
  background-image: url(/parallax_web/static/media/layer-base.e17d3f76.png);
}
.layer.layers_middle {
  background-image: url(/parallax_web/static/media/layer-middle.859e14f4.png);
}
.layer.layers_front {
  background-image: url(/parallax_web/static/media/layer-front.76998f86.png);
}

.main-article {
  background-image: url(/parallax_web/static/media/dungeon.00fdadb2.jpg);
}

